
import { mapState, mapMutations } from 'vuex';
import XHeader from 'vux/src/components/x-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'

import proitemSearch from "./search.vue";
export default {
name: 'proitem',
computed: {
...mapState({
proitemList: state => state.common.proitem.proitemList,
vm: state => state.common.proitem.vm
})
},
components: {
XHeader,
Popup,
PopupHeader,
Checklist,
proitemSearch
},
created() {
this.query();
},
methods: {
...mapMutations({
query: 'common/proitem/getList'
}),
gotoForm(item) {
this.$store.commit('common/proitem/clear');
if (item) {
this.$router.push({ path: "/system/proitem/form", query: { proitemId: item.id } });
} else {
this.$router.push({ path: "/system/proitem/form" });
}
},
handleDelete(userId) {
const _this = this;
this.$vux.confirm.show({
title: "删除提示",
content: "确定要删除该条记录?",
onCancel() {
_this.$vux.toast.text('已取消删除', 'top');
},
onConfirm() {
_this.$store.commit({
type: 'common/proitem/delete',
id: userId
})
}
})
}
}
}



